</template>

<script>
	import Qs from 'qs'
	export default {
		name: 'home',
		data() {
			return {
				msg: '',
				user_info: {},
				openAlert: false,
				data: {},
				openSimple: false,
				img_url: ''
			}
		},
		methods: {
			openSimpleDialog (data) {
				this.img_url = data.user_headpic;
		      	this.openSimple = true;
		    },
			openAlertDialog() {
				this.openAlert = true;
			},
			closeAlertDialog() {
				this.openAlert = false;
			},
			go_personal() {
				this.$router.push('/personal')
			},
			go_upgrade() {
				this.$router.push('/upgrade')
			},
			go_myquestions() {
				this.$router.push('/myquestions')
			},
			go_myresponse() {
				this.$router.push('/myresponse')
			},
			go_feedback() {
				this.$router.push('/feedback')
			},
			go_usinghelpo() {
				this.$router.push('/usinghelpo')
			},
			go_about() {
				this.$router.push('/about')
			},
			go_privacy() {
				this.$router.push('/privacy')
			},
//			去联系客服
			go_relation() {
				this.$router.push('/relation')
			},
//			重新登录
			go_login(){
				window.localStorage.clear();
				this.$router.push('/');
			},
			//			个人信息
			userInfo() {
				var that = this;
				var params = {
					userid: that.user_info.user_id,
//					discussionid: that.discussionid
				};
				that.$http.post('/user/userInfo', Qs.stringify(params), {

				}).then((res) => {
					if(res.meta.code == 200) {
						that.data = res.data;
					}
				})
			}
		},
		created() {
			// 页面刚加载的时候执行
			if(!!localStorage.getItem('user_info')) {
				this.user_info = JSON.parse(localStorage.getItem('user_info'));
			}
			this.userInfo();
		},
		mounted() {
			// 页面刚加载完 dom 也加载完 的时候执行

		}
	}
</script>

<style scoped>
	.mine-container {
		width: 100%;
		height: 100%;
		/*background: #FFFFFF;*/
	}
	
	.mine-header {
		height: 50px;
		background: #FFFFFF;
		line-height: 50px;
		border-bottom: 1px solid #eaeaea;
	}
	
	.mine-zhong {
		display: flex;
		justify-content: space-between;
	}
	
	.mine-fanhui img {
		width: auto;
		height: 2rem;
		margin-right: 1.5rem;
		margin-top: 1rem;
	}
	
	.mine-ti {
		font-size: 1.8rem;
		color: #000000;
		margin-left: 1.5rem;
		font-weight: 550;
		letter-spacing: 1px;
		text-align: center;
	}
	
	.mine-content {
		width: 100%;
		height: calc(100% - 105px);
		overflow-x: hidden;
		overflow-y: auto;
	}
	
	.mine-shang {
		border: 1px solid #FFFFFF;
		background: #FFFFFF;
		margin-bottom: 1rem;
