		margin-bottom: 1rem;
	}
	
	.mine-between {
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding-left: 1rem;
		padding-right: 1.5rem;
	}
	
	.mine-tu img {
		width: auto;
		height: 1.5rem;
		margin: 1rem 1rem 0.7rem 0;
	}
	
	.mine-ziti {
		font-size: 1.4rem;
		color: #7f7f7f;
	}
	
	.mine-enter img {
		width: auto;
		height: 1.1rem;
		margin-top: 0.4rem;
	}
	
	.mine-reverse {
		display: flex;
		flex-direction: row-reverse;
	}
	
	.mine-xian {
		border-bottom: 1px solid #EAEAEA;
		width: 90%;
	}
	
	.mine-tuichu {
		font-size: 1.3rem;
		color: #ff5151;
	}
	
	.mine-footer {
		height: 55px;
		box-sizing: border-box;
		overflow: hidden;
		position: fixed;
		bottom: 0;
		width: 100%;
		background: #fff;
		border-top: 1px solid #f0f0f0;
	}
	.mine-qrtc{
		text-align: center;
		font-size: 1.8rem;
		color: #000000;
		padding: 3.5rem 0;
	}
	.mine-qx{
		position: absolute;
		left: 2rem;
	}
	/deep/ .container {
		padding: 0;
	}
	
	/deep/ .mu-tabs-inverse {
		background-color: #FFFFFF;
	}
	
	/deep/ .mu-tab-wrapper {
		padding: 0;
	}
	
	/deep/ .mu-bottom-item-icon {
		margin-top: 6px;
		background-repeat: no-repeat;
		width: 20px;
		height: 20px;
		background-size: 100% 100%;
	}
	/*/deep/ .mu-bottom-item-text {
		color: #999999
	}*/
	/*/deep/ .mu-bottom-item-active .mu-bottom-item-text {
		color: #229efe;
	}*/
	
	/deep/ .mu-bottom-item-active {
		color: rgba(0, 0, 0, .54);
	}
	
	/deep/ .mu-bottom-item:nth-child(1) .mu-bottom-item-icon {
		background-image: url(../assets/tab_shouye1.png);
	}
	
	/deep/ .mu-bottom-item:nth-child(2) .mu-bottom-item-icon {
		background-image: url(../assets/tab_huashu1.png);
	}
	
	/deep/ .mu-bottom-item:nth-child(3) .mu-bottom-item-icon {
