	}
	
	/deep/ .mu-bottom-item {
		min-width: 0px;
	}
	/deep/ .mu-dialog-actions{
		display: flex;
		justify-content: center;
	}
	/*/deep/ .mu-bottom-item-text {
		color: #000000
	}
	
	/deep/ .mu-bottom-item-active .mu-bottom-item-text {
		color: #8CADF5;
	}*/
	.response-tantu{
		margin-bottom: -1rem;
	}
	.response-tantu img{
		width: 100%;
		height: auto;
		margin: 0 auto;
	}
	/deep/ .mu-dialog-body{
		padding: 0;
	}
</style>


